import {
  warningColor,
  primaryColor,
  dangerColor,
  successColor,
  infoColor,
  roseColor,
  grayColor,
  defaultFont,
} from "./material";

const tableStyle = (theme) => ({
  warningTableHeader: {
    color: warningColor[0],
  },
  primaryTableHeader: {
    color: primaryColor[0],
  },
  dangerTableHeader: {
    color: dangerColor[0],
  },
  successTableHeader: {
    color: successColor[0],
  },
  infoTableHeader: {
    color: infoColor[0],
  },
  roseTableHeader: {
    color: roseColor[0],
  },
  grayTableHeader: {
    color: grayColor[0],
  },
  table: {
    marginBottom: "0",
    width: "100%",
    height: "100%",
    maxWidth: "100%",
    backgroundColor: "transparent",
    borderSpacing: "0",
    borderCollapse: "collapse",
  },
  tableHeadCell: {
    height: "10px",
    color: "inherit",
    ...defaultFont,
    "&, &$tableCell": {
      fontSize: "1em",
    },
  },
  tableCell: {
    ...defaultFont,
    textAlign: "center",
    lineHeight: "1.42857143",
    padding: "8px 8px",
    verticalAlign: "middle",
    fontSize: "0.8125rem",
  },
  tableResponsive: {
    width: "100%",

    marginTop: 0,
    overflow: "hidden",
    overflowX: "auto",
    [theme.breakpoints.down("lg")]: {
      minHeight: "62vh",
    },
    [theme.breakpoints.up("xl")]: {
      minHeight: "62vh",
    },
  },
  tableHeadRow: {
    height: "10px",
    color: "inherit",
    display: "table-row",
    outline: "none",
    horizontalAlign: "middle",
    verticalAlign: "middle",
  },
  tableBodyRow: {
    color: "inherit",
    display: "table-row",
    outline: "none",
    verticalAlign: "middle",
  },
});

export default tableStyle;
