import http from "./httpService";
import publicIp from "react-public-ip";
const devUrl = "";
//http://localhost:3000

export async function login(user) {
  const sess = document.cookie.match("connect.sid");
  const userIp = await publicIp.v4();
  const url = devUrl + "/api/login";
  return http.post(
    url,
    {
      email: user.email,
      password: user.password,
      userIp,
      sess,
    },
    { withCredentials: true }
  );
}

export function getUser() {
  const url = devUrl + "/api/userPage/me";
  return http.get(url, { withCredentials: true });
}

export function autoLogout(id) {
  const url = devUrl + "/api/autoLogout";
  http.post(url, { _id: id });
  return http.post(url, { withCredentials: true });
}

export function autoLogin() {
  const url = devUrl + "/api/autoLogin";
  return http.get(url, { withCredentials: true });
}

export function logout() {
  const url = devUrl + "/api/logout";
  return http.get(url, { withCredentials: true });
}

export function register(user) {
  const url = devUrl + "/api/userPage";
  return http.post(
    url,
    {
      name: user.name,
      email: user.email,
      phone: user.phone,
      password: user.password,
      storeName: user.storeName,
      address: user.address,
      licenceNumber: user.licenceNumber,
      currencyPrecision: user.currencyPrecision,
      ip: user.ipAdrress,
      isLoggedIn: user.isLoggedIn,
      userLanguage: user.userLanguage,
    },
    { withCredentials: true }
  );
}

export function getUsers() {
  const url = devUrl + "/api/userPage";
  return http.get(url, { withCredentials: true });
}

export function deleteUser(id) {
  const url = devUrl + "/api/userPage/" + id;
  return http.delete(url, { withCredentials: true });
}

export async function update(user) {
  const url = devUrl + "/api/userPage/updateUser";
  let userIp;
  if (!user.tmpIp) {
    userIp = await publicIp.v4();
  } else {
    userIp = user.tmpIp;
  }
  return http.put(
    url,
    {
      _id: user.tmpid,
      name: user.tmpname,
      email: user.tmpemail,
      phone: user.tmpphone,
      password: user.tmppassword,
      newPassword: user.tmpnewPassword,
      storeName: user.tmpstoreName,
      address: user.tmpaddress,
      licenceNumber: user.tmplicenceNumber,
      currencyPrecision: user.tmpcurrencyPrecision,
      ip: userIp,
      userLanguage: user.tmpUserLanguage,
    },
    { withCredentials: true }
  );
}

export async function setUserLanguage(language) {
  const url = devUrl + "/api/userPage/setUserLanguage";
  return http.put(
    url,
    {
      userLanguage: language,
    },
    { withCredentials: true }
  );
}

export function forgotPassword(email) {
  const url = devUrl + "/api/forgotPassword";
  return http.post(url, { email }, { withCredentials: true });
}
