import { drawerWidth, transition, container } from "./material";

const mainStyle = (theme) => ({
  wrapper: {
    position: "relative",
    top: "0",
    height: "100vh",
  },
  mainPanel: {
    [theme.breakpoints.up("md")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    overflow: "auto",
    position: "relative",
    float: "right",
    ...transition,
    maxHeight: "100%",
    width: "100%",
    overflowScrolling: "touch",
  },
  mainPanelShift: {
    [theme.breakpoints.up("md")]: {
      width: `calc(100% - 24px)`,
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    overflow: "auto",
    position: "relative",
    float: "right",
    ...transition,
    maxHeight: "100%",
    width: "100%",
    overflowScrolling: "touch",
  },
  content: {
    marginTop: "5px",
    padding: "10px 15px",
    paddingBottom: "0px",
    minHeight: "calc(100vh - 123px)",
  },
  container,
  bar: {
    color: "white",
    display: "inline",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  closeButton: {
    color: "inherit",
    position: "fixed",
    top: "-5px",
    marginLeft: "-4px",
  },
  sideBarShift: {
    visibility: "hidden",
  },
});

export default mainStyle;
